/* tooltip */
.tooltip {
  position: relative;
  display: inline-block;
  font-size: 13px;
  opacity: 1;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 270px;
  background-color: #fff;
  color: #000;
  border-radius: 6px;
  padding: 0;
  position: absolute;
  z-index: 1;
  top: 0px;
  right: 128%;
  box-shadow: 0 0 12px 0 rgb(0 0 0 / 19%) !important;
}

/* .tooltip .tooltiptext::after {
