.checkmark:before {
  content: 'L';
  font-family: "gothambook", sans-serif;
  transform: scaleX(-1) rotate(-35deg);
  display: inline-block;
  vertical-align: middle;
  line-height: 1em;
  width: 1em;
  color: #000000c7;
  height: 1em;
  position: absolute;
  left: 90%;
  top: 10px;
}
